  z-index: 999999;
  background-color: hsl(var(--black));
  width: 100%;
  height: 100%;
}

.loader-p {
  border: 0 solid transparent;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  position: absolute;
  top: calc(50vh - 75px);
  left: calc(50vw - 75px);
}

.loader-p:before,
.loader-p:after {
  content: "";
  border: 1em solid hsl(var(--base));
  border-radius: 50%;
  width: inherit;
  height: inherit;
  position: absolute;
