/* ======================  Select2 Start  ======================*/
.select2-wrapper {
  --border-radius: 8px;
  --height: 50px;
  --select2-border: hsl(var(--border-color));
}

.select2 + .select2-container {
  border: 1px solid var(--select2-border);
  border-radius: var(--border-radius);
  width: 100% !important;
  height: 50px;
}

.select2 + .select2-container .select2-selection__rendered {
  line-height: var(--height);
}

.select2 + .select2-container .select2-selection--single {
  height: var(--height);
}

.select2 + .select2-container .select2-selection {
  border: 0;
  background-color: transparent;
  width: 100%;
}

.select2 + .select2-container .selection {
  width: 100%;
}

.select2 + .select2-container .select2-selection__arrow {
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  background-color: #F8F8F8;
  border-radius: inherit;
  height: calc(var(--height) - 10px);
  width: calc(var(--height) - 10px);
}

.select2 + .select2-container .select2-selection__arrow::after {
  position: absolute;
  content: "\f078";
  inset: 0;
  font-weight: 900;
  font-family: "Font Awesome 6 Free";
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.select2 + .select2-container .select2-selection__arrow b {
  border: 0;
}

.select2-container--open:has(.select2-dropdown--above) {
  margin-top: -12px;
}

.select2-container--open:has(.select2-dropdown--below) {
  margin-top: 10px;
}

.select2-container--open .select2-dropdown {
  border: 1px solid var(--select2-border) !important;
  border-radius: var(--border-radius) !important;
  overflow: hidden;
}

.select2-container--open .select2-dropdown--above {
  margin-bottom: 10px;
}

.select2-search__field {
  border-radius: var(--border-radius);
  border: 1px solid var(--select2-border) !important;
}

.select2-search__field:focus {
  outline: 0;
  border-color: hsl(var(--base)) !important;
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: hsl(var(--base));
}

.select2-container--below.select2-container--open .select2-selection__arrow::after {
  transform: rotate(180deg);
}

/* ======================  Select2 End  ======================*/
/* ================= Slick Arrow & Dots css Start ================ */
.slick-initialized.slick-slider {
  margin: 0 -10px;
}

.slick-initialized.slick-slider .slick-track {
  display: flex;
}

.slick-initialized.slick-slider .slick-slide {
  height: auto;
  padding: 0 10px;
}

.slick-initialized.slick-slider .slick-slide > div {
  height: 100%;
}

.slick-arrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  color: hsl(var(--white));
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: 0.2s linear;
  background-color: hsl(var(--base));
  color: hsl(var(--white));
  font-size: 20px;
}

@media screen and (max-width: 991px) {
  .slick-arrow {
    width: 50px;
    height: 50px;
    font-size: 16px;
  }
}
@media screen and (max-width: 575px) {
  .slick-arrow {
    width: 45px;
    height: 45px;
  }
}
.slick-arrow:hover {
  border-color: hsl(var(--base));
  background-color: hsl(var(--base-two));
}

.slick-next {
  right: -20px;
}

@media screen and (max-width: 991px) {
  .slick-next {
    right: -10px;
  }
}
@media screen and (max-width: 575px) {
  .slick-next {
    right: 10px;
  }
}
.slick-prev {
  left: -20px;
}

@media screen and (max-width: 991px) {
  .slick-prev {
    left: -10px;
  }
}
@media screen and (max-width: 575px) {
  .slick-prev {
    left: 10px;
  }
}
/* -------------- Slick Slider Arrow Style two ------------  */
.slick-arrow {
  position: absolute;
  z-index: 1;
  border: none;
  background-color: transparent;
  color: hsl(var(--white));
  width: 32px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  transition: 0.4s;
  background-color: hsl(var(--black));
  color: hsl(var(--white));
  top: -67px;
}

@media screen and (max-width: 1199px) {
  .slick-arrow {
    top: -69px;
  }
}
@media screen and (max-width: 767px) {
  .slick-arrow {
    top: auto;
    bottom: -50px;
    right: 50%;
  }
}
.slick-arrow:hover {
  background-color: hsl(var(--black));
  color: hsl(var(--white));
}

.slick-next {
  right: 10px;
  background-color: hsl(var(--base));
}

@media screen and (max-width: 767px) {
  .slick-next {
    top: auto;
    right: calc(50% - 37px);
  }
}
.slick-prev {
  right: 52px;
}

@media screen and (max-width: 767px) {
  .slick-prev {
    top: auto;
    right: calc(50% + 5px);
  }
}
/* Dots Css Start */
.slick-dots {
  text-align: center;
  padding-top: 20px;
}

.slick-dots li {
  display: inline-block;
}

.slick-dots li button {
  border: none;
  background-color: hsl(var(--black));
  color: hsl(var(--white));
  margin: 0 3px;
  width: 8px;
  height: 8px;
  border-radius: 1px;
  border-radius: 50%;
  text-indent: -9999px;
  transition: 0.3s linear;
}

.slick-dots li.slick-active button {
  background-color: hsl(var(--base));
  width: 25px;
  border-radius: 5px;
  color: hsl(var(--base));
}

/* Dots Css End */
